import React, { useEffect } from "react";
import "./Buyfrom.css";
import daimg from "../../assets/daimg.png";
import { TiSocialGooglePlus } from "react-icons/ti";
import { FaFacebookF, FaTwitter, FaInstagram } from "react-icons/fa";
import img3 from "../../assets/buy1.png";
import img1 from "../../assets/buy2.png";
import img2 from "../../assets/buy3.png";

import AOS from "aos";
import "aos/dist/aos.css";

const Buyfrom = props =>{
  useEffect(() => {
    AOS.init({
      duration: 1000,
    });
  }, []);
  return (

    <>
        <section  style={{}}>
        <div className="title" data-aos="fade-up">
          <h2>{props.title}</h2>
        </div>
        <div className="row" style={{width:"100%",padding:'2%', alignItems:"center"}}>
            <div className="col-md-4">
            <img src={img3}  id="sideimage"></img>
            </div>
            <div className="col-md-4">
            <img src={img1}  id="sideimage"></img>
            </div>
            <div className="col-md-4">
            <img src={img2}  id="sideimage"></img>
            </div>

        </div>
        </section>
        </>
  );
};

export default Buyfrom;
